.news-box__subtitle {
    font-size: rem(14px) !important;
}

.news-box__title {
    font-size: 2rem !important;
}

.news-box {
    max-width: rem(1200px);
    margin: auto;
    padding: 50px 20px;
}

.news-box__wrap {
    margin: auto;
}

.article {
    display: grid;
    background-color: $solidWhite;
    border-radius: 0 0 8px 8px;
    align-content: start;
}

.article-box {
    display: grid;
    gap: rem(20px);
}

.article__top {
    position: relative;
}

.article__time {
    position: absolute;
    font-weight: normal;
    background: $solidWhite;
    bottom: 0;
    left: 0;
    padding: 0.6rem 1.2rem 0;
    text-align: center;
    line-height: 1;
    font-family: $headingFontFamily;
}

.article__day {
    font-size: 1.3rem;
    display: block;
    font-weight: 800;
    padding-bottom: rem(1px);
}

.article__month {
    font-size: 0.9rem;
    display: block;
    font-weight: 400;
}

.article__img {
    max-width: 100%;
    width: 100%;
    height: 100%;
    border-radius: 8px 8px 0 0;
}

.article__bottom {
    padding: rem(15px);
}

.article__link {
    text-decoration: none;
    color: $black;
}

.article__category {
    font-family: $anotherFontFamily;
    font-size: rem(10px);
    font-weight: 400;
}

.article__anchor::before {
    background-color: $darkPink;
    content: '';
    display: inline-block;
    margin: -2px 6px 0 5px;
    width: rem(4px);
    height: rem(4px);
    border-radius: 50%;
    vertical-align: middle;
}

.article__title {
    font-size: 1rem;
    line-height: rem(24px);
}

.article__content {
    font-family: $anotherFontFamily;
    font-size: rem(13px);
    line-height: rem(24px);
}