.wrap-second {
    padding-top: rem(330px);
}

.btn-wrap {
    display: flex;
    align-items: baseline;
    margin-bottom: rem(40px);
}

.timetable__btn_bottom {
    margin-left: rem(20px);
}

.timetable__senior_title {
    padding-top: rem(20px);
    padding-bottom: rem(20px);
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    position: relative;

    @include before(1px, 25px) {
        top: 28%;
        left: 50%;
        background-color: $white;
    }
}

.timetable__senior_content {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.timetable__senior_gym {
    text-align: right;
}

.svg-location1 {
    left: rem(32px);
}

.timetable__senior_day,
.timetable__senior_name {
    margin-bottom: 0;
}