.main-logo {
    width: rem(250px);
    height: rem(100px);
    grid-area: none;
    margin: 0;

    &_mobile {
        display: none;
    }
}

.header-box {
    grid-template-areas:
        "menu"
        "content"
        "title";
    row-gap: rem(50px);
    grid-template-columns: initial;
}

.header__text {
    font-size: 1.8rem;
    margin-bottom: rem(-10px);
}

.header__title {
    font-size: 5.5rem;
}

.dark-overlay,
.menu-button,
.mobile-menu {
    display: none;
}

.menu {
    max-width: rem(1200px);
    display: flex;
    grid-area: menu;
    align-items: center;
    margin: auto;
    margin-top: 0;
    font-family: $anotherFontFamily;
    transition: 0.6s;
}

.menu ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.menu li {
    position: relative;
}

/* 1st level. */
.menu>ul>li {
    display: inline-block;
}

.menu>ul>li>a {
    display: inline-block;
    padding: 1rem 1.5rem;
    font-weight: bold;
}

/* 2nd level. */
.menu li ul {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    top: 100%;
    left: rem(-20px);
    width: 14rem;
    padding: 1rem 0;
    background: $white;
    transition: all 200ms ease;
    border-radius: rem(5px);
    display: none;
    box-shadow: 0 2px 4px $overlay;
}

.menu li:hover>ul {
    opacity: 1;
    visibility: visible;
    display: initial;
}

.menu li ul a {
    display: block;
    padding: 0.5rem 0.5rem;
}

/* 3rd level. */
.menu li ul li ul {
    top: -1rem;
    left: 100%;
}

.nav-box_left {
    order: -1;
}

.menu-desktop__link {
    color: $white;
    font-size: rem(13px);

    &:hover {
        color: $darkPink;
    }
}

.submenu__item,
.submenu__item_right {
    position: relative;
    padding: 0 15px;

    &:hover {

        @include before(4px, 100%) {
            top: 0;
            left: 0;
            background: $darkPink;
        }
    }
}

.submenu__item_right {
    &:hover {
        &:before {
            left: auto;
            right: 0;
        }
    }
}

.submenu__item .menu-desktop__link {
    color: $black;
    border-bottom: 1px solid $grey;

    &:hover {
        color: $darkPink;
        font-weight: bold;
    }
}

.submenu__item .last {
    border-bottom: 0;
}

.first-link {
    color: $solidGrey;
}

.nav-box__icons {
    display: initial;
}

.icons-wrapper {
    width: rem(15px);
    height: rem(16px);
    fill: $white;
    margin-right: rem(5px);

    &:hover {
        fill: $darkPink;
    }
}

.menu.sticky {
    background-color: $white;
    position: fixed;
    z-index: 20;
    width: 100%;
    box-shadow: 0 1px 6px $overlay;
    max-width: none;
    display: flex;
    justify-content: center;
    height: rem(50px);
}

.menu.sticky a:not(.first-link) {
    color: $black;
}

.menu.sticky a:hover {
    color: $darkPink;
}

.menu.sticky .icons-wrapper {
    fill: $black;
}

.menu.sticky .icons-wrapper:hover {
    fill: $darkPink;
}

.menu.sticky .main-logo_link {
    background: url(#{$iPath}/logo_baseball_dark.png) no-repeat;
    transform: scale(0.6);
}

.menu.sticky .main-logo {
    opacity: 0;
}


.t-score-grid {
    grid-template-columns: 1fr 1fr;
    margin-bottom: rem(-50px);
    gap: rem(30px);
}

.btn-general {
    transition: all 0.4s ease-in;

    &:hover {
        box-shadow: 0 0 0 2px $darkPink inset;
        background-color: transparent;
        color: $darkPink;
    }
}

.menu.sticky .menu-desktop__link.active {
    color: $darkPink;
}