.wrap-seventh {
    background: url(#{$iPath}inner_hero_footer.jpg) no-repeat;
    background-size: cover;
    padding-top: rem(80px);
    padding-bottom: rem(80px);
}

.footer-box {
    color: $white;
    max-width: rem(1200px);
    margin: auto;
    padding: 0 20px;
    display: grid;
    gap: rem(20px);
}

.footer-box__title {
    font-weight: normal;
    font-size: 2rem;
    margin-top: rem(20px);
}

.footer-box__content {
    line-height: 2rem;
    font-size: rem(14px);
}

.footer-box_grid {
    max-width: rem(830px);
    display: grid;
    align-content: start;
}

.footer-box__wrap {
    width: rem(40px);
    height: rem(40px);
    outline: 1px solid $white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.footer-box__icons {
    display: grid !important;
    gap: rem(10px);
    grid-template-columns: repeat(3, auto);
    justify-content: start;
}

.icons-footer {
    margin-right: 0 !important;
    width: rem(15px);
    height: rem(16px);
    fill: $white;
    transform: scale(1.2);
}

.footer__form_flex {
    display: flex;
    flex-direction: column;
}

.footer__label {
    margin-bottom: rem(10px);
}

.footer__input {
    height: rem(40px);
    outline: 1px solid $transWhite;
    border-radius: rem(8px);
    color: $white;
    text-indent: rem(7px);
    width: 100%;

    &:focus {
        box-shadow: 0 0 6px 0 $darkPink;
    }
}

.footer__input::placeholder {
    color: $white;
}

.footer__btn {
    padding-top: rem(16px);
    padding-bottom: rem(16px);
}

.error {
    display: block;
    width: 100%;
    color: $white;
    background-color: $red;
    border-radius: rem(8px);
    font-size: rem(14px);
}

.error.active {
    padding: rem(8px);
    transition: all 0.3s ease-in-out;
    margin-top: rem(5px);
}