.timetable-box {
    margin: auto;
    grid-template-columns: 1fr 1fr;
}

.timetable__subtitle {
    font-size: 1.4rem;
}

.timetable__title {
    font-size: 4.2rem;

    &_other {
        margin-bottom: rem(-5px);
    }
}

.timetable__content {
    font-size: 1.3rem;
}

.timetable__text {
    font-size: rem(14px);
}