.wrap-fifth {
    background: url(#{$iPath}inner-hero-team.png) no-repeat;
    background-size: cover;
    padding-top: rem(50px);
    padding-bottom: rem(140px);
}

.roster-box_top {
    margin: auto;
    text-align: center;
}

.roster-box {
    max-width: rem(1200px);
    margin-left: rem(30px);
    margin-right: rem(30px);
    margin: auto;
}

.roster-box_bottom {
    margin-top: rem(50px);
    color: $white;
    font-family: $headingFontFamily;
    max-width: rem(1200px);
}

.roster-box__grid {
    text-align: center;
}

.roster-box__wrap {
    width: rem(120px);
    height: rem(120px);
    margin: auto;
}

.roster-box__img {
    max-width: 100%;
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

.roster-box__position {
    font-weight: 300;
    font-size: rem(13px);
}

.roster-box__name {
    font-weight: 800;
    font-size: rem(20px);
    margin-top: rem(-5px);
}

//  =========== SLIDER ===============

.dots-box {
    margin: 1rem 0;
    padding: 0;
    list-style-type: none;
    display: flex;
    justify-content: center;
}

.dots-box>li {
    margin-right: rem(10px);
}

.dots-box button {
    background: transparent;
    width: rem(12px);
    height: rem(12px);
    border-radius: 50%;
    cursor: pointer;
    text-indent: rem(-9999px);
    border: 2px solid $white;
}

.dots-box .slick-active button {
    background: $white;
}