.timetable-box {
    display: grid;
    grid-template-columns: 1fr 1.34fr;
    gap: rem(20px);
    max-width: rem(1200px);
    padding-left: rem(15px);
    padding-right: rem(15px);
}

.btn-wrap {
    margin-bottom: 0;
}

.timetable__senior_time {
    text-align: right;
}

.timetable__btn_bottom:hover {
    background-color: $darkPink;
    color: $white;
}

.wrap-second {
    padding-top: rem(100px);
    margin-top: rem(32px);
}