.footer-box__title {
    font-size: 2.7rem;
    margin-bottom: 0;
}

.footer-box__content {
    font-size: rem(16px);
}

.footer-box {
    grid-template-columns: 1fr 1fr;
    gap: rem(50px);
}

.footer-box__wrap:hover {
    outline: 2px solid $darkPink;
    background-color: $white;
}

.footer-box__wrap:hover .icons-footer {
    fill: $darkPink;
}