.about-box {
    max-width: rem(1200px);
    margin: auto;
    padding-top: rem(50px);
    padding-right: rem(20px);
    padding-left: rem(20px);
    display: grid;
    grid-template-rows: auto auto;
    gap: rem(50px);
}

.img-fluid {
    max-width: 100%;
    width: 100%;
    height: 100%;
}