.wrap-eight {
    padding-bottom: rem(40px);
}

.map {
    height: rem(500px);
}

.contact-box__link {
    transition: all 300ms ease;

    &:hover {
        opacity: 0.5;
    }
}

.contact-box__ball {
    transition: all 0.2s ease-in-out;

    &:hover {
        outline: 8px solid $darkPink;
    }
}