.princip-box_bottom {
    grid-template-columns: repeat(4, 1fr);
}

.princip-box_top {
    width: rem(500px);
}

.princip-box__circle {
    transition: all 0.4s ease-in;

    &:hover {
        box-shadow: 0 0 0 2px $darkPink inset;
        background-color: transparent;
    }
}

.princip-box__circle:hover .baseball-icons {
    fill: $darkPink;
}

.princip-box__subtitle,
.princip-box__content {
    font-size: rem(16px) !important;
}

.princip-box__title {
    font-size: 2.8rem !important;
}