.wrap-second {
    background: $semiGray;
    min-height: 100vh;
    padding-top: rem(230px);
    padding-bottom: rem(50px);
    margin-top: rem(-10px);
}

.timetable-box {
    margin-left: rem(20px);
    margin-right: rem(10px);
}

.timetable__subtitle {
    font-size: 1.2rem;
    font-family: $headingFontFamily;
    font-weight: 300;
}

.timetable__title {
    font-size: 3rem;
    font-weight: normal;
}

.title-pink {
    color: $darkPink;
    font-weight: 600;
}

.timetable__content {
    font-size: 1.1rem;
    line-height: rem(30px);
}

.timetable__text {
    font-family: $anotherFontFamily;
    font-size: rem(13px);
    line-height: rem(20px);
}

.timetable__btn {
    font-size: rem(12px);
    padding: 1.2rem 2.4rem;
}

.timetable__btn_bottom {
    color: $darkPink;
    background-color: $white;
    outline: 2px solid $darkPink;
    display: block;
    margin-top: rem(10px);
}

.timetbale-box_right {
    font-size: rem(14px);
    margin-top: rem(20px);
    font-family: $headingFontFamily;
}

.timetable__senior {
    margin-right: rem(10px);
    border-radius: rem(8px);
    overflow: hidden;
}

.timetable__senior_title {
    color: $white;
    background-color: $darkPink;
    padding: rem(10px);
}

.timetable__senior_name {
    margin-bottom: rem(10px);
    font-weight: 600;
}

.timetable__senior_location {
    font-weight: 300;
    position: relative;
    padding-left: rem(15px);
}

.svg-location {
    @include icon(32px, 32px, url(#{$iPath}/icons/location.svg)) {
        transform: scale(0.4);
        left: rem(-10px);
        top: rem(-7px);
    }
}

.timetable__senior_content {
    padding: rem(15px);
    border-bottom: 1px solid $grey;
    font-family: $anotherFontFamily;
    font-weight: 500;
    background-color: $anotherWhite;
    opacity: 0.7;
    position: relative;
}

.timetable__senior_first {
    background-color: $white;
    opacity: 1;
    padding-left: rem(30px);
    position: relative;
    font-weight: bold;
}

.svg-checkmark {
    @include icon(32px, 32px, url(#{$iPath}/icons/checkmark.svg)) {
        transform: scale(0.5);
        left: rem(2px);
        top: rem(6px);
    }
}

.timetable__senior_day {
    margin-bottom: rem(10px);
}

.timetable__senior_training {
    margin-bottom: rem(5px);
    margin-top: rem(5px);
    opacity: 0.5;
    font-size: rem(12px);
}

.timetable__senior_gym {
    position: relative;
    padding-left: rem(10px);
}

.svg-location1 {
    @include icon(32px, 32px, url(#{$iPath}/icons/location1.svg)) {
        transform: scale(0.33);
        left: rem(-13px);
        top: rem(-8px);
    }
}

.timetable__kids_first {
    margin-top: rem(30px);
    margin-bottom: rem(30px);
}