.wrap-container {
    background: url(#{$iPath}inner-hero-team.png) no-repeat;
    background-position: center top;
    min-height: 100vh;
}

.container {
    max-width: rem(1200px);
    width: 100%;
    margin: auto;
    padding: 15px 15px;
}

.form-logo {
    display: block;
    margin: auto !important;
}

.form-title {
    margin: 50px 0;
}

.form-text {
    margin-bottom: rem(50px);
    letter-spacing: rem(2px);
    text-align: left;
    padding-left: rem(15px);
}

.btn-back {
    color: $solidGrey;
    font-weight: 900;
    padding-left: rem(15px);
    font-size: rem(22px);
}