.wrap-fourth {
    background: url(#{$iPath}inner_grey_background.png) no-repeat;
    background-size: cover;
}

.princip-box {
    max-width: rem(1200px);
    margin: auto;
    padding: 50px 20px;
}

.princip-box_top {
    text-align: center;
    margin: auto;
}

.princip-box__subtitle,
.princip-box__content {
    font-size: rem(14px);
}

.princip-box__title {
    font-size: 2rem;
    margin: 0;
}

.princip-box_bottom {
    display: grid;
    margin-top: rem(40px);
    gap: rem(30px);
    align-items: start;
    justify-items: center;
}

.princip-box__grid {
    display: grid;
    gap: rem(20px);
    max-width: rem(300px);
    align-items: center;
    justify-items: center;
}

.princip-box__circle {
    width: rem(78px);
    height: rem(78px);
    border-radius: 50%;
    background-color: $darkPink;
    display: flex;
    align-items: center;
    justify-content: center;
}

.baseball-icons {
    width: rem(39px);
    height: rem(39px);
    fill: $white;
}

.princip-box__slogan {
    font-family: $headingFontFamily;
    font-weight: 800;
    font-size: rem(20px);
}

.princip-box__link {
    text-decoration: none;
    color: $black;
}

.princip-box__text {
    text-align: center;
    font-family: $anotherFontFamily;
    font-size: rem(14px);
    color: $semiBlack;
    line-height: rem(24px);
}