.wrap-sixth {
    background: $semiGray;
    min-height: 100vh;
    padding-top: rem(50px);
    padding-bottom: rem(50px);
}

.card-box {
    margin: auto;
    margin-top: rem(-150px);
}

.card-box__grid {
    display: grid;
    grid-template-columns: minmax(250px, 300px);
    justify-content: center;
    margin-right: rem(30px);
    margin-left: rem(30px);
}

.card-box__wrap {
    color: $white;
    padding: 60px 15px;
    text-align: center;
    display: grid;
    justify-items: center;
    align-content: start;
}

.card-box_first,
.card-box__circle_first {
    background-color: $pink;
}

.card-box_second {
    background-color: $darkPink;
}

.card-box_third,
.card-box__circle_third {
    background-color: $semiPink;
}

.card-box_fourth,
.card-box__circle_fourth {
    background-color: $boldPink;
}

.card-box__link,
.card-box__text {
    color: $white;
}

.card-box__slogan {
    margin: 20px 0;
}

.card-box__circle {
    outline: 2px solid $white;
}