.wrap {
    background: url(#{$iPath}inner-hero-about-us.png) no-repeat;
    background-size: cover;
    background-position: center top;
}

.header__title {
    color: $white;
    text-align: center;
    font-size: 3.5rem;
    font-weight: 300;
    letter-spacing: rem(-1px);
    grid-area: title;
}

.strong {
    color: $solidGrey;
    font-weight: 900;
}

.header__text {
    color: $white;
    text-align: center;
    font-size: 1.2rem;
    font-family: $headingFontFamily;
    font-weight: 300;
    grid-area: content;
}

.header-box {
    display: grid;
    grid-template-areas:
        "menu logo"
        "content content"
        "title title";
    grid-template-rows: 160px auto auto;
}

.menu {
    display: none;
}

.menu-button {
    display: inline-block;
    width: rem(28px);
    height: rem(20px);
    position: relative;
    grid-area: menu;
    margin: 20px 20px 0 10px;
}

.main-logo {
    width: rem(120px);
    height: rem(48px);
    max-width: 100%;
    grid-area: logo;
    margin-top: rem(5px);
}

.menu-button.in-mobile {
    position: absolute;
    right: 1rem;
    top: rem(-10px);
    margin-right: 0;
}

.menu-button__item {
    position: absolute;
    height: rem(2px);
    border-radius: rem(2px);
    width: 100%;
    background-color: $darkPink;
    top: 0;
}

.menu-button__item:nth-of-type(1) {
    top: 0;
}

.menu-button__item:nth-of-type(2),
.menu-button__item:nth-of-type(3) {
    top: rem(10px);
}

.menu-button__item:nth-of-type(4) {
    top: rem(20px);
}

.menu-button.active .menu-button__item:nth-of-type(1),
.menu-button.active .menu-button__item:nth-of-type(4) {
    opacity: 0;
}

.menu-button.active .menu-button__item:nth-of-type(2) {
    transform: rotate(45deg);
}

.menu-button.active .menu-button__item:nth-of-type(3) {
    transform: rotate(-45deg);
}

.dark-overlay {
    position: fixed;
    top: 0;
    left: 0;
    visibility: hidden;
    z-index: 9;
    width: 100%;
    height: 100%;
    background-color: $overlay;
    opacity: 0;
    transition: opacity 0.5s, visibility 0.5s;
}

.dark-overlay.visible {
    opacity: 1;
    visibility: visible;
}

.mobile-menu {
    background-color: $backgrBlack;
    padding: 1rem 0 1rem 1rem;
    position: fixed;
    text-align: center;
    width: 16rem;
    top: 0;
    left: -100%;
    z-index: 10;
    height: 100%;
    overflow: scroll;
    transition: left 1s ease-in-out;
}

.mobile-menu.visible {
    left: 0;
    transition: all 1s ease-out;
}

.mobile-menu ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.mobile-navigation {
    margin-top: rem(30px);
    position: relative;

    &::before,
    &::after {
        display: block;
        content: ' ';
        width: rem(85px);
        height: rem(4px);
        background: $darkPink;
        margin: auto;
    }

    &::before {
        margin-bottom: rem(20px);
    }

    &::after {
        margin-top: rem(20px);
    }
}

.mobile-navigation a {
    display: block;
    padding: 1rem;
    color: $white;
    font-size: rem(13px);
    text-decoration: none;
    font-family: $anotherFontFamily;
}

.mobile__first-link {
    font-weight: 600;
}

.mobile-navigation li {
    position: relative;
}

.mobile-navigation li ul {
    display: none;
}

.mobile-navigation li ul a {
    font-size: rem(11px);
    opacity: 0.8;
}

.inner-link {
    font-size: 10px !important;
    opacity: 0.6 !important;
}

.mobile__icons {
    margin-top: rem(40px);
}

.icons-wrapper {
    width: rem(15px);
    height: rem(16px);
    fill: $white;
    margin-right: rem(5px);
}

.fa-chevron-down {
    transform: rotate(0deg);
    transition: transform 0.2s linear;
    position: absolute;
    left: rem(210px);
}

.fa-chevron-down.open {
    transform: rotate(180deg);
    transition: transform 0.2s linear;
}

.mobile-navbar {
    display: flex;
}

.mobile-navbar.sticky {
    background-color: $white;
    position: fixed;
    z-index: 20;
    width: 100%;
    height: rem(50px);
    box-shadow: 0 1px 6px $overlay;
}

.mobile-navbar.sticky .main-logo_link {
    background: url(#{$iPath}/logo_baseball_dark.png) no-repeat;
    background-size: contain;
    margin-left: rem(15px);
}

.mobile-navbar.sticky .main-logo_mobile {
    opacity: 0;
}

.mobile-navbar.sticky .menu-button {
    margin-top: rem(13px);
}

.mobile-navbar.visible {
    visibility: hidden;
}



.t-score-grid {
    max-width: rem(1200px);
    margin: auto;
    padding: 0 20px;
    display: grid;
    gap: rem(20px);
    justify-content: center;
    margin-bottom: rem(-212px);
}

.t-score {
    background-color: $semiWhite;
    border-radius: rem(8px);
    overflow: hidden;
    padding: 30px 20px;
    font-family: $headingFontFamily;
    text-align: center;
    position: relative;
    display: grid;
    align-content: center;
    box-shadow: 0 0 3px $white;

    @include before(1px, 30px) {
        top: 40%;
        left: 50%;
        background-color: $darkPink;
    }
}

.t-score__date {
    margin-bottom: rem(30px);
    font-size: rem(13px);
}

.t-score__result {
    display: flex;
    justify-content: center;

    &_right {
        display: flex;
        align-items: center;
    }
}

.t-score__img {
    flex: 1 0 40%;
}

.t-score__img img {
    width: 100%;
    max-width: 100%;
    height: auto;
}

.t-score__data {
    flex: 0 1 60%;
}

.t-score__title {
    font-size: rem(10px);
    opacity: 0.6;
    font-weight: 500;
}

.t-score__value {
    font-size: 2.5rem;
    font-weight: 800;
}

.t-score__form {
    margin-top: rem(30px);
}

.btn-general {
    color: $white;
    text-transform: uppercase;
    padding: 0.8rem 2rem;
    background-color: $darkPink;
    border-radius: rem(8px);
    font-family: $headingFontFamily;
    font-weight: 600;
    cursor: pointer;
    font-size: rem(10px);
    letter-spacing: rem(1px);
}