.wrap-eight {
    background: linear-gradient(90deg, rgba(25, 24, 29, 1) 0%, rgba(12, 30, 43, 1) 100%);
    padding-top: rem(60px);
}

.contact-box_top {
    margin: auto;
    text-align: center;
    max-width: rem(500px);
}

.contact-box__img {
    width: 100%;
    max-width: 100%;
    height: 100%;
}

.contact-box_bottom {
    margin: auto;
    margin-top: rem(40px);
}

.contact-box__card {
    max-width: rem(570px);
    background-color: $white;
    padding: rem(30px);
    border-radius: rem(10px);
    text-align: center;
    margin: auto;
    margin-bottom: rem(30px);
}

.contact-box__wrap {
    margin-bottom: rem(20px);
}

.contact-box__circle {
    display: inline-block;
    margin: auto;
    margin-top: rem(-30px);
    border-radius: 50%;
    background-color: $white;
    padding: rem(7px);
}

.contact-box__slogan {
    margin-bottom: rem(20px);
}

.contact-box__data {
    font-size: rem(14px);
    font-family: $anotherFontFamily;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: rem(30px);
    margin-left: rem(30px);
}

.contact-box__link {
    color: $semiBlack;
    margin-bottom: rem(15px);
    position: relative;
}

.contact-box__link_first {
    @include before(32px, 32px) {
        top: rem(-10px);
        left: rem(-30px);
        background: url(#{$iPath}/icons/phone.svg) no-repeat;
        transform: scale(0.5);
    }
}

.contact-box__link_second {
    @include before(26px, 28px) {
        top: rem(-8px);
        left: rem(-28px);
        background: url(#{$iPath}/icons/chain.svg) no-repeat;
        transform: scale(0.65);
    }
}

.contact-box__link_third {
    @include before(32px, 32px) {
        top: rem(-9px);
        left: rem(-30px);
        background: url(#{$iPath}/icons/mail.svg) no-repeat;
        transform: scale(0.75);
    }
}

.map {
    height: rem(400px);
    max-width: rem(1000px);
    margin: auto;
    z-index: 3;
}