* {
  box-sizing: border-box;
}

html {
  @include getCssVar(font-size);
}

body {
  margin: 0;
  padding: 0;
  @include getCssVar(font-family);
  font-size: 1rem;
  min-height: 100vh;
}

h1,
h2,
h3 {
  margin-top: 0;
  font-family: $headingFontFamily;
}

a {
  text-decoration: none;
}