.timetbale-box_right {
    display: grid;
    grid-template-areas:
        "first second"
        "first third";
    column-gap: rem(10px);
    row-gap: rem(35px);
}

.timetable__senior {
    grid-area: first;
}

.timetable__kids_first {
    grid-area: second;
    margin-top: 0;
    margin-bottom: initial;
}

.timetable__kids_second {
    grid-area: third;
}