//fonts
$prefix: prefix- !default;
$baseFontSize: 16px;
$headingFontFamily: 'Oswald', sans-serif;
$baseFontFamily: 'Libre Baskerville', serif;
$anotherFontFamily: 'Montserrat', sans-serif;

// Paths

$iPath: "../i/";

// Colors

$white: #ffffff;
$solidGrey: #b7bbc7;
$semiGray: rgb(238, 238, 238);
$semiWhite: rgba(255, 255, 255, 1);
$transWhite: rgba(255, 255, 255, 0.1);
$anotherWhite: rgba(255, 255, 255, 0.8);
$darkPink: #92426b;
$pink: #a54a79;
$semiPink: #7b375a;
$boldPink: #662e4b;
$grey: rgba(0, 0, 0, .1);
$solidWhite: #f7f7f7;
$black: #181818;
$semiBlack: #191919;
$backgrBlack: #111111;
$overlay: rgba(0, 0, 0, .8);
$arrowBackGround: rgba(0, 0, 0, .5);
$arrowOpacity: rgba(0, 0, 0, .3);
$red: #dc3232;

// Default

$blue: #0d6efd !default;
$indigo: #6610f2 !default;
$purple: #6f42c1 !default;


$colors: (
  "black": $black,
  "white": $white,
  "blue": $blue,
  "indigo": $indigo,
  "purple": $purple,
  "pink": $pink,
  "red": $red,
  ) !default;