.wrap-third {
    background: url(#{$iPath}inner-bg-awards.png) no-repeat;
    background-size: cover;
    padding-top: rem(50px);
    padding-bottom: rem(100px);
}

.schedule-box {
    max-width: rem(1200px);
    padding: 0 30px;
}

.schedule-box_top {
    color: $white;
    max-width: rem(550px);
    margin-bottom: rem(30px);
}

.schedule-box__subtitle {
    font-family: $headingFontFamily;
    font-size: rem(14px);
    font-weight: 300;
}

.schedule-box__title {
    font-weight: normal;
    font-size: 2rem;
    margin-top: rem(20px);
}

.schedule-box__content {
    line-height: 2rem;
    font-size: rem(14px);
}

.schedule-box__wrap {
    color: $white;
    font-family: $headingFontFamily;
}

.schedule-box__img {
    max-width: rem(200px);
    margin: auto;
    margin-bottom: rem(20px);
}

.schedule-box__img img {
    max-width: 100%;
    width: 100%;
    height: 100%;
}

.schedule-box__arena {
    font-size: rem(13px);
    font-weight: 300;
}

.schedule-box__link {
    text-decoration: none;
}

.schedule-box__team {
    color: $darkPink;
    font-weight: 800;
    font-size: rem(20px);
    margin: 5px 0 5px 0;
}

.schedule-box__date {
    font-size: rem(13px);
    font-weight: 300;
}

.schedule-box__info {
    text-align: center;
}

.schedule-box_bottom {
    max-width: 1200px;
    width: 100%;
    margin: auto;
    padding-left: rem(10px);
}

// ================ SLIDER =================

.slider .slick-current {
    position: relative;
}

.myarrows__item {
    display: flex;
    position: absolute;
    top: 50%;
    width: rem(35px);
    height: rem(35px);
    background-color: $arrowBackGround;
    color: $anotherWhite;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    z-index: 7;
}

.myarrows__item_prev {
    left: 0;
}

.myarrows__item_next {
    right: 0;
}